import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { AuthenticationService } from 'src/app/auth/_services/authentication.service';
import { CartService } from 'src/app/services/cart.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-wb-navbar',
  templateUrl: './wb-navbar.component.html',
  styleUrls: ['./wb-navbar.component.scss']
})
export class WbNavbarComponent implements OnInit {
 
  assetsUrl: string = environment.localAssetsUrl;

  get isUserLogged(): boolean {
    return this.authService.currentUserValue != null && this.authService.currentUserValue.requireOTP === "False";
  }

  get cartCount(): number {
    return this.cartService.getCartCount();
  }

  get notifCount(): number {
    return 0; //this.cartService.getCartCount();
  }

  constructor(private cartService: CartService, private router: Router,
    private spinner: NgxSpinnerService,
    private authService: AuthenticationService) { }

  ngOnInit(): void {
  }

  @Input() 
  noAction: boolean = false;

  logOutClick() {
    this.spinner.show();
    this.authService.logout().subscribe(res => {
      this.spinner.hide()
    });;
  }

  loginClicked() {
    this.router.navigate([{ outlets: { action: ["login"] } }]);
  }

  addressesClicked() {
    this.router.navigate([{ outlets: { action: ["my-addresses"] } }]);
  }

  redeemRequestClicked() {
    this.router.navigate([{ outlets: { action: ["wallet-redeem"] } }]);
  }

  myProfile() {
    this.router.navigate([{ outlets: { action: ["profile"] } }]);
  }
  myOrdersClick() {
    this.router.navigate([{ outlets: { action: ["my-orders"] } }]);
  }

  navigateToCart() {
    this.router.navigate([{ outlets: { action: ["cart"] } }]);
  }

  navigateToNotif() {
    this.router.navigate([{ outlets: { action: ["notification"] } }]);
  }
}
