import { Component, Input, OnInit } from '@angular/core';
import { ResponseObject } from 'src/app/models/common';
import { Order, OrderFilter } from 'src/app/models/order';
import { OrderService } from 'src/app/services/api-services/order.service';

@Component({
  selector: 'app-wb-orders',
  templateUrl: './wb-orders.component.html',
  styleUrls: ['./wb-orders.component.scss']
})
export class WbOrdersComponent implements OnInit {

 
  @Input() 
  modalId: string;

  orders: Order[];

  @Input() 
  set active(_active: boolean) {
    if(_active){
      let filter: OrderFilter = new OrderFilter();
      this.orderService.getOrderList(filter).subscribe((res: ResponseObject<Order[]>) => {
        this.orders = res.Data ?? [];
        console.log(JSON.stringify(res.Data));
      });
    }
  }

  constructor(private orderService: OrderService) { }

  ngOnInit(): void {
  }

}
