import { Component, Injector, Input, OnInit } from '@angular/core';
import { Wallet } from 'src/app/models/wallet';
import { Location } from '@angular/common';
import { WalletService } from 'src/app/services/api-services/wallet.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { FormBuilder } from '@angular/forms';
import { BankAccountService } from 'src/app/services/api-services/bank-accounts.service';
import { BankAccount } from 'src/app/models/bank-account';

declare var UIkit: any;

@Component({
  selector: 'app-wb-bank-accounts',
  templateUrl: './wb-bank-accounts.component.html',
  styleUrls: ['./wb-bank-accounts.component.scss']
})
export class WbBankAcccountsComponent extends BaseComponent implements OnInit {

  @Input() modalId: string = 'bank-accounts-modal';

  deletionAccount: BankAccount | null = null;

  submitted: boolean = false;

  _switcher: any;

  currentSwitcherIndex: number = 0;

  formInstance = this.fb.group({
    Id: [],
    AccountOfUserId: [],
    BankName: [],
    AccountNumber: [],
    IFSCCode: [],
    AccountName: []
  })


  get switcher(): any {
    if (this._switcher == null) {
      this._switcher = UIkit.switcher('#data-switcher', { animation: false })
    }
    return this._switcher;
  }

  bankAccounts: BankAccount[];
  currentBalance: number;

  constructor(protected injector: Injector, private bankAccountService: BankAccountService,
    private _location: Location) {
    super(injector);
  }

  switch(index: number) {
    this.currentSwitcherIndex = index;
    setTimeout(() => {
      this.switcher.show(index);
    });
  }

  modalClose() {
    if (this.router != null) {
      let outletUrls: string[] = [];
      if (this.router.url.indexOf('wallet-redeem') >= 0) {
        //outletUrls.push('edit-address');
        //outletUrls.push(this.selectedId);
      }
      this.router.navigate([{ outlets: { action: outletUrls, primary: [] } }])
    }
  }

  ngOnInit(): void {

    this.showModal(this.modalId, () => {
      this.modalClose()
    });

    this.loadBankAccounts();

    this.currentSwitcherIndex = 0;
 
  }

  close() {
    this.router.navigate(this.returnUrl.return)
  }

  loadBankAccounts() {
    this.bankAccountService.getBankAccountList().subscribe((res: { Data: BankAccount[]; }) => {
      this.bankAccounts = res.Data;
    })
  }

  addBankAccount() {
    var bankAccount: BankAccount = <BankAccount><unknown>this.formInstance.value;
    this.bankAccountService.addBankAccountInfo(bankAccount).subscribe(res => {
      this.switch(0);
      this.formInstance.reset();
      this.loadBankAccounts();
    })
  }

  navigateBack() {
    if (this.currentSwitcherIndex != 0) {
      this.switch(0);
    } else {
      this._location.back();
    }
  }

  confirmDelete() {
    if (this.deletionAccount != null) {
      this.bankAccountService.deleteAccount(this.deletionAccount.Id).subscribe(res => {
        this.deletionAccount = null;
        this.switch(0);
        this.loadBankAccounts();
      });
    }
  }
}