import { Component, Input, OnInit } from '@angular/core';
import { Product, ProductVariant } from 'src/app/models/product';
import { CartService } from 'src/app/services/cart.service';
import { UtilitiesService } from 'src/app/services/utilities.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-wb-product-overview-modal',
  templateUrl: './wb-product-overview-modal.component.html',
  styleUrls: ['./wb-product-overview-modal.component.scss']
})
export class WbProductOverviewModalComponent implements OnInit {

  imgUrl: string = environment.uploadsUrl;
  selectedIndex: number = 0;

  get selectedProduct(): ProductVariant | undefined {
    if (this.product != null && this.product.ProductVariants.length > 0) {
      return this.product.ProductVariants[this.selectedIndex];
    }
    else return;
  }

  @Input()
  product: Product;

  get cartCount(): number[] {
    let count: number[] = [];
    if (this.product != null && this.product.ProductVariants?.length > 0) {
      for (let item of this.product.ProductVariants) {
        count.push(this.cartService.getProductCartCount(item.Id));
      }
    }
    return count;
  }

  constructor(private utilities: UtilitiesService, private cartService: CartService) { }

  ngOnInit(): void {
  }

  addToCart() {
    this.cartService.addItem(this.selectedProduct?.Id ?? 0);
  }

  removeFromCart() {
    this.cartService.decreaseItem(this.selectedProduct?.Id ?? 0);
  }

}
