import { Component, Injector, Input, OnInit, Type } from '@angular/core';
import { enumToKeyValueArray, ResponseObject } from 'src/app/models/common';
import { Order, OrderDetail, OrderStatus } from 'src/app/models/order';
import { OrderService } from 'src/app/services/api-services/order.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-wb-order-details',
  templateUrl: './wb-order-details.component.html',
  styleUrls: ['./wb-order-details.component.scss']
})
export class WbOrderDetailsComponent extends BaseComponent implements OnInit {

  path: string = 'edit-address'

  order?: Order;

  @Input() modalId: string = 'order-detail';

  uploadsUrl: string = environment.uploadsUrl;

  public orderStatuses: any = enumToKeyValueArray(OrderStatus);

  constructor(protected injector: Injector,
    private orderService: OrderService) {
    super(injector);
    this.initOrderStatus(OrderStatus.All);
  }

  ngOnInit(): void {
    this.showModal(this.modalId, () => {
      this.modalClose()
    });

    let pos = this.router.url.lastIndexOf('/') + 1;
    let orderQuery = this.router.url.substring(pos, this.router.url.length);
    if (!isNaN(<any>orderQuery)) {
      let id = parseInt(orderQuery);
      this.orderService.getOrderInfo(id).subscribe((res: ResponseObject<Order>) => {
        console.log(res.Data);
        let order = res.Data;
        order?.Purchases.forEach((item: OrderDetail) => {
          item.ProductImage.ImageUrl = item.ProductImage.ImageUrl.replace('~', '');
        })
        this.order = order;
        this.initOrderStatus(this.order?.OrderState ?? 0);
      })
    } else {
      this.apiRequestService.showMessage(<ResponseObject<any>>{
        Message: "Invalid Order Id"
      });
      this.router.navigate([{ outlets: { action: ['my-orders'], primary: [] } }]);
    }
  }

  @Input() public set routeData(data: string) {
    let id = parseInt(data);
    if (!isNaN(id)) {
    }
  }

  initOrderStatus(orderStatus: OrderStatus) {
    let found: boolean = false;
    let excludeList = [OrderStatus.All, OrderStatus.Placed, OrderStatus.Cancelled, OrderStatus.Rejected];
    for (let status of this.orderStatuses) {
      status.done = false;
      status.done = !found;
      status.show = !excludeList.includes(status.id);
      if (status.id == orderStatus)
        found = true;
    }
  }

  modalClose() {
    if (this.router != null) {
      this.router.navigate([{ outlets: { action: [] } }], { skipLocationChange: false });
      if (this.router.url.indexOf('/order-details') > -1) {
        this.router.navigateByUrl(this.router.url.split("/order-details")[0])
      }
    }
  }
}
