import { AfterViewInit, Component, Injector, Input, OnDestroy, OnInit } from '@angular/core';
import { OrderStatus, ResponseObject, transformCamelToSpaces } from 'src/app/models/common';
import { Order, OrderFilter } from 'src/app/models/order';
import { OrderService } from 'src/app/services/api-services/order.service';
import { BaseComponent } from 'src/app/shared/base.component';

@Component({
  selector: 'app-wb-myorders',
  templateUrl: './wb-myorders.component.html',
  styleUrls: ['./wb-myorders.component.scss']
})
export class WbMyordersComponent extends BaseComponent implements OnInit, OnDestroy {

  @Input()
  modalId: string = "my-orders-modal";

  orderStatus: typeof OrderStatus = OrderStatus;
  camelCaseToSpacedText: typeof transformCamelToSpaces = transformCamelToSpaces;

  orders: Order[];
  selectedId: number;

  constructor(private orderService: OrderService, protected injector: Injector) {
    super(injector);
  }

  ngOnInit(): void {
    this.showModal(this.modalId, () => {
      this.modalClose()
    });

    this.showSpinner();
    let filter: OrderFilter = new OrderFilter();
    this.orderService.getOrderList(filter).subscribe((res: ResponseObject<Order[]>) => {
      this.orders = res.Data ?? [];
      this.hideSpinner();
    });
  }

  ngOnDestroy(): void {
    this.orders = [];
  }


  modalClose() {
    if (this.router != null) {
      let outletUrls = [];
      if (this.router.url.indexOf('order-details') >= 0) {
        outletUrls.push('order-details');
        outletUrls.push(this.selectedId);
      }
      this.router.navigate([{ outlets: { action: outletUrls, primary: [] } }])
    }
  }

  orderDetails(id: number) {
    this.selectedId = id;
    this.router.navigate([{  outlets: { action: ["order-details", id], primary: [] } }]);
  }
}
