import { Component, Injector, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from 'src/app/auth/_services/authentication.service';
import { Address, AddressFilter } from 'src/app/models/address';
import { ResponseObject } from 'src/app/models/common';
import { AddressesService } from 'src/app/services/api-services/address.service';
import { BaseComponent } from 'src/app/shared/base.component';

@Component({
  selector: 'app-wb-my-address',
  templateUrl: './wb-my-address.component.html',
  styleUrls: ['./wb-my-address.component.scss']
})
export class WbMyAddressComponent extends BaseComponent implements OnInit {

  @Input() modalId: string = 'address-modal';

  selectedId: string;
  
  @Input() set active(_active: boolean) {
    if (_active) {
      this.ngOnInit();
    }
  }

  addresses: Address[] = [];

  get defaultAddress(): Address | null {
    if (this.addresses.length > 0) {
      let addresses = this.addresses.filter(i => i.IsDefault);
      if (addresses == null || addresses.length > 0) {
        return addresses[0];
      }
      else {
        return this.addresses[0];
      }
    }
    return null;
  }

  
  modalClose() {
    if (this.router != null) {
      let outletUrls: string[] = [];
      if (this.router.url.indexOf('edit-address') >= 0) {
        outletUrls.push('edit-address');
        outletUrls.push(this.selectedId);
      }
      this.router.navigate([{ outlets: { action: outletUrls, primary: [] } }])
    }
  }

  isEditMode: boolean = false;
  id: any = 0;

  constructor(protected injector: Injector, private authService: AuthenticationService, private addressService: AddressesService) {
    super(injector);
  }

  ngOnInit(): void {

    this.showModal(this.modalId, () => {
      this.modalClose()
    });

    this.loadAddresses();
  }

  loadAddresses() {

    let filter = new AddressFilter();
    filter.UserId = this.authService.currentUserValue.userId;
    this.addressService.getAddressList(filter).subscribe((res: ResponseObject<any>) => {
      console.log("Addresses: ")
      this.addresses = res.Data;
      this.hideFullSpinner();
    });
  }

  addAddress(){ 
    
    this.router.navigate([{ outlets: { action: ['address'], primary: [] } }]);
  }

  deleteAddress(id: any) {
    this.showFullSpinner();
    this.addressService.deleteAddress(id).subscribe(() => {
      this.loadAddresses();
    })
  }

  editAddress(id: number) {
    this.selectedId = id + "";
    this.router.navigate([{ outlets: { action: ['edit-address', id], primary: [] } }]);
  }

  close() {
    this.router.navigate(this.returnUrl.return)
  }
}
