import { Component, inject, Injector, OnInit } from '@angular/core';
import { NavigationEnd } from '@angular/router';
import { filter } from 'rxjs/operators';
import { Category, CategoryFilter } from 'src/app/models/category';
import { ProductQueryType, ResponseObject } from 'src/app/models/common';
import { OfferBanner, OfferBannerPlacement, OfferFilter } from 'src/app/models/offer';
import { Product, ProductFilter } from 'src/app/models/product';
import { ProductGroup, ProductGroupFilter } from 'src/app/models/product-group';
import { SubCategory, SubCategoryFilter } from 'src/app/models/sub-category';
import { CategoriesService } from 'src/app/services/api-services/categories.service';
import { OfferService } from 'src/app/services/api-services/offers.service';
import { ProductGroupsService } from 'src/app/services/api-services/product-groups.service';
import { ProductsService } from 'src/app/services/api-services/products.service';
import { SubCategoriesService } from 'src/app/services/api-services/sub-categories.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { environment } from 'src/environments/environment';

declare var UIkit: any;

@Component({
  selector: 'app-product-catalogue',
  templateUrl: './product-catalogue.component.html',
  styleUrls: ['./product-catalogue.component.scss']
})
export class ProductCatalogueComponent extends BaseComponent implements OnInit {

  assetsUrl: string = environment.assetsUrl;
  uploadsUrl: string = environment.uploadsUrl;

  header: string = "";

  categories: Category[] = [];
  subCategories: SubCategory[] = [];
  productGroups: ProductGroup[] = [];

  categoryId: number;
  subCategoryId: number;
  selectedOffer: OfferBanner | null;
  productGroupId: number | undefined;

  categoryKey: string | undefined;
  subCategoryKey: string | undefined;
  productGroupKey: string | undefined;

  categoryFilter: CategoryFilter = new CategoryFilter();
  subCategoryFilter: SubCategoryFilter = new SubCategoryFilter();
  productGroupFilter: ProductGroupFilter = new ProductGroupFilter();
  productFilter: ProductFilter = new ProductFilter();

  offerBanners: OfferBanner[] = [];

  isNoMore: boolean = false;

  childSubs: any = null;
  subCategorySub: any = null;
  isProductLoading: boolean = false;

  asyncCount: number = 0;

  searchKey: string;

  productList: Product[] = [];

  get activeProductGroups(): ProductGroup[] {
    return this.productGroups.filter(j => this.productGroupId == j.Id);
  }

  get inActiveProductGroups(): ProductGroup[] {
    return this.productGroups.filter(j => this.productGroupId != j.Id);
  }

  get selectedCategory(): Category | undefined {
    return this.categories.find(i => i.RouteKey == this.categoryKey);
  }
  get selectedSubCategory(): SubCategory | undefined {
    return this.subCategories.find(i => i.RouteKey == this.subCategoryKey);
  }

  // item: Product = {
  //   ProductImages: ['/Storage/Uploads/ProductImages/46e1f218e32d4001863a60cceb3ebb91.jpg'],
  //   ProductName: "abcd",
  //   BrandId: 1,
  //   Description: '',
  //   Id: 1,
  //   ProductCode: "111",
  //   ProductVariants: [],
  //   SubCategoryId: 1,
  //   BrandName: '',
  //   SubCategoryName: '',
  // };
  constructor(protected injector: Injector,
    protected categoryService: CategoriesService,
    protected subCategoryService: SubCategoriesService,
    protected productGroupService: ProductGroupsService,
    private productService: ProductsService, protected offerSeervice: OfferService) {
    super(injector);
  }

  ngOnInit(): void {

    this.productFilter.PageNumber = 0;

    this.route.firstChild?.params.subscribe(res => {
      this.selectedOffer = null;

      this.categoryKey = res.categoryKey;

      this.categoryId = (this.categories.find(i => i.RouteKey == this.categoryKey))?.Id ?? 0;

      this.checkOfferId(() => {
        this.loadProductFromStart();
      });
    });

    this.route.queryParamMap.subscribe(res => {

      this.searchKey = res.get('q') ?? '';

      this.loadProductFromStart();
    })

    if (this.subCategorySub == null) {
      this.subCategorySub = this.route.firstChild?.firstChild?.firstChild?.params.subscribe(res => {

        this.subCategoryKey = res.subCategoryKey;
        this.subCategoryId = (this.subCategories.find(i => i.RouteKey == this.subCategoryKey))?.Id ?? -1;

      });
    }

    if (this.childSubs == null) {
      this.childSubs = this.route.firstChild?.firstChild?.firstChild?.firstChild?.firstChild?.params.subscribe(res => {

        this.productGroupKey = res.groupKey;
        this.productGroupId = (this.productGroups.find(i => i.RouteKey == this.productGroupKey))?.Id;
      })
    }

    this.loadCategories(() => {
      this.categoryId = (this.categories.find(i => i.RouteKey == this.categoryKey))?.Id ?? 0;

      this.loadSubCategories(() => {

        this.asyncCount++;
        this.subCategoryId = (this.subCategories.find(i => i.RouteKey == this.subCategoryKey))?.Id ?? -1;

        // this.loadProductGroups(() => {
        //   this.productGroupId = (this.productGroups.find(i => i.RouteKey == this.productGroupKey))?.Id;
        // })
        this.loadAfterAsync();
      });

      this.loadOffers(() => {
        this.asyncCount++;
        this.checkOfferId(() => {
          this.loadAfterAsync();
        });
      });
    })

  }

  checkOfferId(callBack?: any) {
    if (this.categoryId == 0 && this.offerBanners != null) {
      this.selectedOffer = this.offerBanners.find(i => i.OfferUrl == this.categoryKey) ?? null;
      this.header = this.selectedOffer?.DisplayName ?? this.header;
      if (callBack) callBack();
    } else {
      this.selectedOffer = null;
    }
  }

  loadAfterAsync() {
    if (this.asyncCount >= 2) {
      this.asyncCount = 0;
      this.loadProductFromStart();
    }
  }

  loadCategories(callBack?: any) {

    if (this.categories.length == 0) {
      this.categoryService.getCategoryList(this.categoryFilter).subscribe((res: ResponseObject<Category[]>) => {
        this.categories = res.Data ?? [];
        this.categoryId = (this.categories.find(i => i.RouteKey == this.categoryKey))?.Id ?? 0;
        if (callBack) callBack();
      });
    }
    else if (callBack) callBack();
  }

  loadOffers(callBack?: any): void {

    let filter = new OfferFilter();
    this.offerSeervice.getOfferBannerList(filter).subscribe((res: ResponseObject<OfferBanner[]>) => {
      res.Data?.forEach((item: OfferBanner) => {
        item.BannerImageUrl = item.BannerImageUrl.replace("~/", '');
      })
      this.offerBanners = (res.Data ?? [])//.filter(i => i.Placement == OfferBannerPlacement.DesktopOffers);
      if (callBack) callBack();
    })
  }

  loadSubCategories(callBack?: any) {

    if (this.subCategories.length == 0) {
      this.subCategoryService.getSubCategoryList(this.subCategoryFilter).subscribe((res: ResponseObject<SubCategory[]>) => {
        this.subCategories = res.Data ?? [];
        let subCategory = (this.subCategories.find(i => i.RouteKey == this.subCategoryKey));
        this.subCategoryId = subCategory?.Id ?? -1;
        this.header = subCategory?.SubCategoryName ?? '';
        if (callBack) callBack();
      });
    }
    else if (callBack) callBack();
  }

  loadProductGroups(callBack?: any) {
    this.selectedOffer = null; 
    this.productGroupId = undefined;
    this.productGroupFilter.SubCategoryId = this.subCategories.find(i => i.RouteKey == this.subCategoryKey)?.Id ?? -1;
    this.productGroupService.getProductGroupList(this.productGroupFilter).subscribe((res: ResponseObject<ProductGroup[]>) => {
      this.productGroups = res.Data ?? [];
      this.productGroupId = (this.productGroups.find(i => i.RouteKey == this.productGroupKey))?.Id;
      if (callBack) callBack();
    });
  }

  navigateCategory(category: Category, event: any, i: number) {
    event.stopPropagation();
    UIkit.nav('#left-menu').toggle(i + 1);
    this.navigateCategoryLink(category);
  }

  navigateHome() {
    this.categoryKey = '';
    this.categoryId = -1;
    this.subCategoryKey = '';
    this.subCategoryId = -1;
    this.productGroupId = undefined;
    this.header = '';
    this.router.navigate(['/collections']);
    this.loadProductFromStart();
  }

  navigateCategoryLink(category: Category) {
    this.selectedOffer = null; 
    this.categoryId = category.Id;
    this.subCategoryKey = "";
    this.subCategoryId = -1;
    this.categoryKey = category.RouteKey;
    this.productGroupId = undefined;
    this.header = '';
    this.router.navigate(['/collections/' + category?.RouteKey]);
    this.loadProductFromStart();
  }

  navigateSubCategory(subCategory: SubCategory) {
    this.selectedOffer = null; 
    this.categoryId = subCategory.CategoryId;
    this.categoryKey = this.categories.find(i => i.Id == this.categoryId)?.RouteKey ?? '';
    this.subCategoryKey = subCategory.RouteKey;
    this.subCategoryId = subCategory.Id;
    this.productGroupId = undefined;
    this.header = subCategory?.SubCategoryName ?? '';
    this.router.navigate(['/collections/' + this.categoryKey + '/' + subCategory.RouteKey]);
    this.loadProductGroups();
    this.loadProductFromStart();
  }

  loadProductFromStart() {

    this.isNoMore = false;
    this.productList = [];
    this.productFilter.PageNumber = -1;
    this.productFilter.Search = this.searchKey;
    this.showSpinner();
    this.loadProducts();
  }

  loadProducts() {
    this.isProductLoading = true;
    this.productFilter.PageSize = 20;
    this.productFilter.PageNumber++;
    this.productFilter.CategoryKey = this.categoryKey ?? "";
    this.productFilter.SubCategoryKey = this.subCategoryKey ?? "";
    this.productFilter.ProductGroupKey = this.productGroupKey ?? "";
    this.productFilter.ProductGroupId = this.productGroupId;
    if (this.selectedOffer != null) {
      this.productFilter.OfferId = this.selectedOffer.OfferId;
      this.productFilter.ProductQueryType = ProductQueryType.Offers;
    }
    else {
      this.productFilter.ProductQueryType = ProductQueryType.FilterOrNone;
    }
    this.productService.getProductList(this.productFilter).subscribe((res: ResponseObject<Product[]>) => {
      let data = res.Data ?? [];

      this.isProductLoading = true;
      if (!environment.production) {
        console.log("Products Loaded: ");
        console.log(data);
      }
      let products: Product[] = this.processProductList(data);
      products.forEach(item => {
        this.productList.push(item);
      })
      this.isNoMore = this.productList.length > 0 && products.length == 0;
    })
  }

  processProductList(list: Product[]): Product[] {
    let _res: Product[] = []
    list.forEach(item => {
      let images: string[] = [];
      item.ProductImages.forEach(img => {
        images.push(img.replace('~', ''));
      });
      item.ProductImages = images
      _res.push(item);
    });
    return _res;
  }

  onScroll(event: any) {
    // visible height + pixel scrolled >= total height 
    if (event.target.offsetHeight + event.target.scrollTop >= event.target.scrollHeight - 5 && !this.isNoMore) {
      this.loadProducts();
    }
  }
}
