import { Component, Injector, Input, OnDestroy, OnInit } from '@angular/core';
import { AuthenticationService } from 'src/app/auth/_services/authentication.service';
import { Notification, NotificationView } from 'src/app/models/notification';
import { ProductQueryType, ResponseObject } from 'src/app/models/common';
import { ProductFilter, ProductVariant } from 'src/app/models/product';
import { ProductsService } from 'src/app/services/api-services/products.service';
import { NotificationService } from 'src/app/services/notification.service';
import { UtilitiesService } from 'src/app/services/utilities.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { environment } from 'src/environments/environment';

declare var UIkit: any;

@Component({
  selector: 'app-wb-my-notifs',
  templateUrl: './wb-my-notifs.component.html',
  styleUrls: ['./wb-my-notifs.component.scss']
})
export class WbMyNotificationComponent extends BaseComponent implements OnInit, OnDestroy {

  notifId: string = 'my-notif';

  get notifList(): Notification[] {
    return this.notifService.getNotificationList()
  }

  get notifTotal(): number {
    return this.productNotifList.reduce((sum, item) => sum + (item.Quantity * item.ProductVariant.Price), 0);
  }

  productNotifList: NotificationView[] = [];

  constructor(public injector: Injector, private notifService: NotificationService,
    private authService: AuthenticationService,
    private productService: ProductsService) {
    super(injector);
  }

  assetsUrl: string = environment.assetsUrl;

  imgUrl: string = environment.assetsUrl;

  ngOnDestroy(): void {
    UIkit.offcanvas(`#${this.notifId}`).hide();;
  }

  notifClosed() {

    UIkit.offcanvas(`#${this.notifId}`).hide();
    setTimeout(() => {
      this.router.navigate([{ outlets: { action: [] } }]);
    }, 500)
  }

  ngOnInit(): void {

    setTimeout(() => {
      UIkit.offcanvas(`#${this.notifId}`).show();
      UIkit.util.on(`#${this.notifId}`, "hidden", () => {
        if (this.router.url.indexOf('place-order') < 0) {
          this.router.navigate([{ outlets: { action: [] } }]);
        }
      });
    })

    this.loadProducts();

    this.notifService.notificationUpdated.subscribe((notifItem: Notification) => {
      if (notifItem == null || notifItem == undefined) {
        this.loadProducts();
      } else {
        let index = this.productNotifList.findIndex(i => i.ProductVariantId == notifItem.ProductVariantId);
        if (index > -1) {
          if (notifItem.Quantity == 0) {
            this.productNotifList.splice(index, 1);
          }
          else {
            this.productNotifList[index].Quantity = notifItem.Quantity;
          }
        } else {
          this.loadProducts();
        }
      }
    });
  }

  loadProducts() {

    if (this.notifList.length == 0) {
      this.productNotifList = [];
      return;
    }

    this.showSpinner();

    let filter: ProductFilter = new ProductFilter();
    filter.Ids = this.notifList.map(i => i.ProductVariantId).join(',');
    this.productService.getProductVariantList(filter).subscribe((res: ResponseObject<ProductVariant[]>) => {
      let productNotifList: NotificationView[] = [];
      let variantList = res.Data ?? [];
      variantList.forEach(vItem => {
        this.notifList.forEach(cItem => {
          if (vItem.Id == cItem.ProductVariantId) {
            productNotifList.push({
              Quantity: cItem.Quantity,
              ProductVariantId: cItem.ProductVariantId,
              ProductVariant: vItem
            })
          }
        });
      })
      this.productNotifList = productNotifList;
      this.hideSpinner();
    });
  }

  removeProduct(id: number) {
    this.notifService.removeItem(id);
  }

  addToNotif(id: number) {
    this.notifService.addItem(id);
  }

  decreaseFromNotif(id: number) {
    this.notifService.decreaseItem(id);
  }

  placeOrderClicked() {
    this.router.navigate([{ outlets: { action: ['place-order'] } }]);
  }
}
