import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { PlaceOrderService } from 'src/app/services/api-services/place-order.service';
import { CartService } from 'src/app/services/cart.service';
import { BaseComponent } from 'src/app/shared/base.component';

declare var UIkit: any;

@Component({
  selector: 'app-wb-order-succssfull',
  templateUrl: './wb-order-succssfull.component.html',
  styleUrls: ['./wb-order-succssfull.component.scss']
})
export class WbOrderSuccssfullComponent implements OnInit {

  @Input() modalId: string = 'order-successful-modal';
  @Input() set active(_active: boolean) {
    if (_active) {
      if (this.placeOrderStatus == null || this.placeOrderStatus.orderId == null) {
        setTimeout(() => {
          this.backHome();
        })
      }
    } else {
      // this.placeOrderService.setOrderResponseStatus(null).subscribe(() => {
      // });
    }
  }

  get placeOrderStatus(): any {
    return this.placeOrderService.placedOrderValue;
  }

  constructor(private router: Router,
    private placeOrderService: PlaceOrderService) { }

  ngOnInit(): void {
    setTimeout(() => {
      UIkit.modal("#" + this.modalId).show();
      UIkit.util.on("#" + this.modalId, 'hidden', () => {
        this.modalClose();
      });
    });
  }

  backHome() {
    this.router.navigateByUrl('/', { replaceUrl: true });
  }

  orderStatus() {
    this.router.navigate([{ outlets: { action: ['my-orders'], primary: [] } }]) 
  }

  modalClose() {
    if (this.router != null) {
      let outletUrls: string[] = [];
      if (this.router.url.indexOf('my-orders') >= 0) {
        outletUrls.push('my-orders');
        //outletUrls.push(this.selectedId);
      }
      this.router.navigate([{ outlets: { action: outletUrls, primary: [] } }])
    }
  }

}