import { Component, Injector, Input, OnInit } from '@angular/core';
import { AuthenticationService } from 'src/app/auth/_services/authentication.service';
import { Wallet } from 'src/app/models/wallet';
import { AddressesService } from 'src/app/services/api-services/address.service';
import { WalletService } from 'src/app/services/api-services/wallet.service';
import { BaseComponent } from 'src/app/shared/base.component';

@Component({
  selector: 'app-wb-my-wallet',
  templateUrl: './wb-my-wallet.component.html',
  styleUrls: ['./wb-my-wallet.component.scss']
})
export class WbMyWalletComponent extends BaseComponent implements OnInit {

  @Input() modalId: string = 'wallets-modal';

  walletList: Wallet[];
  currentBalance: number;

  constructor(protected injector: Injector, private authService: AuthenticationService, private walletService: WalletService) {
    super(injector);
  }

  modalClose() {
    if (this.router != null) {
      let outletUrls: string[] = [];
      if (this.router.url.indexOf('wallet-redeem') >= 0) {
        //outletUrls.push('edit-address');
        //outletUrls.push(this.selectedId);
      }
      this.router.navigate([{ outlets: { action: outletUrls, primary: [] } }])
    }
  }

  ngOnInit(): void {

    this.showModal(this.modalId, () => {
      this.modalClose()
    });

    this.loadWalletInfo();
  }

  close() {
    this.router.navigate(this.returnUrl.return)
  }

  loadWalletInfo() {
    this.walletService.getWalletList().subscribe((res: { Data: Wallet[]; }) => {
      this.walletList = res.Data;
      this.currentBalance = 0;
      this.walletList.forEach(item => this.currentBalance += item.Amount);
    })
  }
}
