import { Component, Injector, Input, OnInit } from '@angular/core';
import { WalletService } from 'src/app/services/api-services/wallet.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { Location } from '@angular/common'; 
import { FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { User } from 'src/app/auth/_models/user';
import { AuthenticationService } from 'src/app/auth/_services/authentication.service';
import { UserService } from 'src/app/auth/_services/user.service';
import { CartService } from 'src/app/services/cart.service'; 
import { environment } from 'src/environments/environment';

declare var UIkit: any;

@Component({
  selector: 'app-wb-profile',
  templateUrl: './wb-profile.component.html',
  styleUrls: ['./wb-profile.component.scss']
})
export class WbProfileComponent  extends BaseComponent implements OnInit {

  
  @Input() modalId: string = 'user-profile-modal';

  assetsUrl: string = environment.assetsUrl;

  userInfo: User;

  formInstance: FormGroup = this.fb.group({
    UserName: [''],
    FirstName: ['', [Validators.required]],
    LastName: ['', [Validators.required]],
    PhoneNumber: ['', [Validators.required]],
    Email: ['', [Validators.required]]
  })

  get isUserLogged(): boolean {
    return this.authService.currentUserValue != null && this.authService.currentUserValue.requireOTP === "False";
  }

  get cartCount(): number {
    return this.cartService.getCartCount();
  }

  constructor(protected injector: Injector, private cartService: CartService,
    private authService: AuthenticationService,
    private userrService: UserService,
    private userService: UserService,
    private _location: Location) {
    super(injector);
  }
 
  loadCurrentUseerDetails(force: boolean = false) {
    this.userrService.getCurrentUserDetails(force).subscribe(data => {
      this.userInfo = data;
      if (this.userInfo.PhoneNumber == null) {
        this.userInfo.PhoneNumber = data.UserName;
      }
      this.formInstance.patchValue(this.userInfo);
    });
  }


  logOutClick() {
    this.showSpinner();
    this.authService.logout().subscribe(res => {
      this.router.navigate([""], { replaceUrl: true })
      this.hideSpinner()
    });
  }

  submitForm() {
    let editModal = 'edit-user'
    this.userService.saveUserDetails(this.formInstance.value).subscribe((data) => {
      if (data.IsSuccess) {
        this.loadCurrentUseerDetails(true);
        UIkit.modal('#' + editModal).hide();
      }
    })
  }
 
  
  isAddOrEdit: boolean = false;
  isEditMode: boolean = false
  
  ngOnInit(): void { 

    this.showModal(this.modalId, () => {
      this.modalClose()
    });
    this.loadCurrentUseerDetails();
    
  }
  
  
  modalClose() {
    if (this.router != null) {
      let outletUrls: string[] = []; 
      this.router.navigate(["/"]);
    }
  }
  
  close() {
    this._location.back();
  }

  navigateBack() {
    this._location.back();
  }
}