import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { Notification } from 'src/app/models/notification';
import { environment } from '../../environments/environment';
import { Brand, BrandFilter } from '../models/brand';
import { ApiRequestService } from './api-services/api-request.service';

@Injectable({
    providedIn: 'root'
})
export class NotificationService {

    private _updateNotification: Subject<Notification> = new Subject();
    public notificationUpdated: Observable<any> = this._updateNotification.asObservable();

    private storage_key: string = "notification_list";

    private notificationList: Notification[] = [];

    constructor() {
        this.loadNotificationList();
    }

    addItem(productId: number) {
        this.loadNotificationList();

        let notificationItem = undefined;
        let index = this.notificationList.findIndex(i => i.ProductVariantId == productId);
        if (index > -1) {
            notificationItem = this.notificationList[index];
            notificationItem.Quantity += 1;
            this.notificationList[index] = notificationItem;
        } else {
            notificationItem = new Notification();
            notificationItem.ProductVariantId = productId;
            notificationItem.Quantity = 1;
            this.notificationList.push(notificationItem);
        }
        this.saveNotificationList();
        this._updateNotification.next(notificationItem);
    }

    decreaseItem(productId: number) {
        this.loadNotificationList();

        let notificationItem = undefined;
        let index = this.notificationList.findIndex(i => i.ProductVariantId == productId);
        if (index > -1) {
            if (this.notificationList[index].Quantity > 1) {
                notificationItem = this.notificationList[index];
                notificationItem.Quantity -= 1;
                this.notificationList[index] = notificationItem;
            } else {
                this.notificationList.splice(index, 1);
            }
        }
        this.saveNotificationList();
        this._updateNotification.next(<Notification>notificationItem);
    }

    removeItem(productId: number) {
        this.loadNotificationList();
        let index = this.notificationList.findIndex(i => i.ProductVariantId == productId);
        if (index > -1) {
            this.notificationList.splice(index, 1);
        }
        this.saveNotificationList();
        this._updateNotification.next(null);
    }


    getProductNotificationCount(productId: number): number {
        this.loadNotificationList();

        let count = 0;
        let index = this.notificationList.findIndex(i => i.ProductVariantId == productId);
        if (index > -1) {
            count = this.notificationList[index].Quantity;
        }
        return count;
    }

    getNotificationCount(): number {
        let sum = 0;
        this.notificationList.forEach(item => {
            sum += item.Quantity;
        })
        return sum;
    }

    loadNotificationList() {
        if (this.notificationList.length == 0) {
            this.notificationList = <Notification[]>JSON.parse(localStorage.getItem(this.storage_key) ?? "[]");
        }
    }

    clearList() {
        this.notificationList = [];
        localStorage.removeItem(this.storage_key);
        this._updateNotification.next(null);
    }

    getNotificationList(): Notification[] {
        return this.notificationList;
    }

    saveNotificationList() {
        localStorage.setItem(this.storage_key, JSON.stringify(this.notificationList));
    }
}