import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { DesktopHomeComponent } from './views/desktop-home/desktop-home.component'; 
import { SharedModule } from '../shared/shared.module';
import { DesktopLayoutComponent } from './views/desktop-layout/desktop-layout.component';
import { LoginComponent } from '../auth/login/login.component';
import { WbNavbarComponent } from './components/wb-navbar/wb-navbar.component';
import { WbSearchComponent } from './components/wb-search/wb-search.component';
import { WbFooterComponent } from './components/wb-footer/wb-footer.component';
import { WbShopByCategoryComponent } from './components/wb-shop-by-category/wb-shop-by-category.component';
import { WbSuggestedProductsComponent } from './components/wb-suggested-products/wb-suggested-products.component';
import { WbTwoBannersComponent } from './components/wb-two-banners/wb-two-banners.component';
import { WbShopByBrandsComponent } from './components/wb-shop-by-brands/wb-shop-by-brands.component';
import { WbFooterBannerComponent } from './components/wb-footer-banner/wb-footer-banner.component';
import { WbMainBannerComponent } from './components/wb-main-banner/wb-main-banner.component';
import { WbOffersComponent } from './components/wb-offers/wb-offers.component';
import { WbMyordersComponent } from './components/wb-myorders/wb-myorders.component';
import { WbOrderDetailsComponent } from './components/wb-order-details/wb-order-details.component';
import { WbMyWalletComponent } from './components/wb-my-wallet/wb-my-wallet.component';
import { WbWalletHistoryComponent } from './components/wb-wallet-history/wb-wallet-history.component';
import { WbWalletRedeemComponent } from './components/wb-wallet-redeem/wb-wallet-redeem.component';
import { WbMyAddressComponent } from './components/wb-my-address/wb-my-address.component';
import { WbAddNewAddressComponent } from './components/wb-add-new-address/wb-add-new-address.component';
import { WbMyCartComponent } from './components/wb-my-cart/wb-my-cart.component';
import { WbPlaceOrdersComponent } from './components/wb-place-orders/wb-place-orders.component';
import { WbProductOverviewModalComponent } from './components/wb-product-overview-modal/wb-product-overview-modal.component';
import { WbProductItemCardComponent } from './components/wb-product-item-card/wb-product-item-card.component';
import { WbProductVariantsModalComponent } from './components/wb-product-variants-modal/wb-product-variants-modal.component';
import { ProductCatalogueComponent } from './views/product-catalogue/product-catalogue.component';
import { RoleGuardService } from '../auth/_helpers/role.guard';
import { Role } from '../auth/_models/role';
import { WbOrderSuccssfullComponent } from './components/wb-order-succssfull/wb-order-succssfull.component';
import { WbSecondaryBannerComponent } from './components/wb-secondary-banner/wb-secondary-banner.component';
import { WbHomeBrandsComponent } from './components/wb-home-brands/wb-home-brands.component';
import { WbOrdersComponent } from './components/wb-orders/wb-orders.component';
import { WbMyOrderDetailsComponent } from './components/wb-my-order-details/wb-my-order-details.component';
import { MbOrderSuccessComponent } from '../mobile/components/mb-order-success/mb-order-success.component';
import { TwoFactorComponent } from '../auth/two-factor/two-factor.component';
import { WbTimingSelectorComponent } from './components/wb-timing-selector/wb-timing-selector.component';
import { WbProfileComponent } from './components/wb-profile/wb-profile.component';
import { WbBankAcccountsComponent } from './components/wb-bank-accounts/wb-bank-accounts.component';
import { environment } from 'src/environments/environment'; 
import { ServerDownComponent } from './components/server-down/server-down.component';
import { WbMyNotificationComponent } from './components/wb-my-notifs/wb-my-notifs.component';

const categoryRoutes = (outletAction: string) => {
  return [
    {
      canActivate: [RoleGuardService],
      data: { roles: [Role.User] },
      path: 'my-addresses', component: WbMyAddressComponent,
      outlet: outletAction
    },
    {
      canActivate: [RoleGuardService],
      data: { roles: [Role.User] },
      path: 'address', component: WbAddNewAddressComponent,
      outlet: outletAction
    },
    {
      path: 'cart', component: WbMyCartComponent,
      outlet: outletAction
    },
    {
      path: 'notification', component: WbMyNotificationComponent,
      outlet: outletAction
    },
    {
      canActivate: [RoleGuardService],
      data: { roles: [Role.User] },
      path: 'place-order', component: WbPlaceOrdersComponent,
      outlet: outletAction
    },
    {
      canActivate: [RoleGuardService],
      data: { roles: [Role.User] },
      path: 'edit-address/:id', component: WbAddNewAddressComponent,
      outlet: outletAction
    },
    {
      canActivate: [RoleGuardService],
      data: { roles: [Role.User] },
      path: 'wallets', component: WbMyWalletComponent,
      outlet: outletAction
    },
    {
      canActivate: [RoleGuardService],
      data: { roles: [Role.User] },
      path: 'bank-accounts', component: WbBankAcccountsComponent,
      outlet: outletAction
    },
    {
      canActivate: [RoleGuardService],
      data: { roles: [Role.User] },
      path: 'wallet-redeem', component: WbWalletRedeemComponent,
      outlet: outletAction
    },
    {
      canActivate: [RoleGuardService],
      data: { roles: [Role.User, Role.Admin] },
      path: 'profile', component: WbProfileComponent,
      outlet: outletAction
    },
    {
      canActivate: [RoleGuardService],
      data: { roles: [Role.User] },
      path: 'order-success', component: WbOrderSuccssfullComponent,
      outlet: outletAction
    },
    {
      canActivate: [RoleGuardService],
      data: { roles: [Role.User] },
      path: 'my-orders', component: WbMyordersComponent,
      outlet: outletAction
    },
    {
      canActivate: [RoleGuardService],
      data: { roles: [Role.User] },
      path: 'order-details/:id', component: WbMyOrderDetailsComponent,
      outlet: outletAction
    },
    {
      canActivate: [RoleGuardService],
      path: 'otp', component: TwoFactorComponent,
      outlet: outletAction
    },
    {
      path: 'login', component: LoginComponent,
      outlet: outletAction
    },
    {
      path: 'offers', component: WbOffersComponent,
      outlet: outletAction
    }
  ]
}

const desktopRoutes: Routes = [
  {
    path: 'server-down', component: ServerDownComponent
  },
  {
    path: '', component: DesktopLayoutComponent, children: [
      {
        path: '', component: DesktopHomeComponent,
        children: [...categoryRoutes('primary')]
      },
      {
        path: 'ps', component: ProductCatalogueComponent,
      },
      {
        path: 'collections', component: ProductCatalogueComponent,
        children:
          [
            {
              path: ':categoryKey', component: ProductCatalogueComponent,
              children: [
                {
                  path: '', component: ProductCatalogueComponent,
                  children: [
                    {
                      path: ':subCategoryKey', component: ProductCatalogueComponent,
                      children: [
                        {
                          path: 'products', component: ProductCatalogueComponent
                        },
                        {
                          path: ':groupKey', component: ProductCatalogueComponent
                        }
                      ]
                    }
                  ]
                }
              ]
            }
          ]
      },
      ...categoryRoutes('action')
    ]
  }
]

@NgModule({
    imports: [
        SharedModule,
        CommonModule,
        RouterModule.forChild(desktopRoutes),
    ],
    declarations: [
        DesktopLayoutComponent,
        DesktopHomeComponent,
        WbNavbarComponent,
        WbSearchComponent,
        WbFooterComponent,
        WbShopByCategoryComponent,
        WbSuggestedProductsComponent,
        WbTwoBannersComponent,
        WbShopByBrandsComponent,
        WbFooterBannerComponent,
        WbMainBannerComponent,
        WbOffersComponent,
        WbMyordersComponent,
        WbOrderDetailsComponent,
        WbMyWalletComponent,
        WbWalletHistoryComponent,
        WbWalletRedeemComponent,
        WbMyAddressComponent,
        WbAddNewAddressComponent,
        WbMyCartComponent,
        WbMyNotificationComponent,
        WbPlaceOrdersComponent,
        WbProductOverviewModalComponent,
        WbProductItemCardComponent,
        WbProductVariantsModalComponent,
        ProductCatalogueComponent,
        WbOrderSuccssfullComponent,
        WbSecondaryBannerComponent,
        WbHomeBrandsComponent,
        WbOrdersComponent,
        WbMyOrderDetailsComponent,
        WbBankAcccountsComponent,
        WbTimingSelectorComponent,
        WbProfileComponent,
        ServerDownComponent
    ]
})
export class DesktopModule {

  connection: any = null;

}
