import { Component, Injector, Input, OnInit } from '@angular/core';
import { NavigationEnd, ParamMap } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { filter } from 'rxjs/operators';
import { AuthenticationService } from 'src/app/auth/_services/authentication.service';
import { ModalRequest, ModalType, ResponseObject, Result } from 'src/app/models/common';
import { Product, ProductVariant } from 'src/app/models/product';
import { UtilitiesService } from 'src/app/services/utilities.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { environment } from 'src/environments/environment';

declare var UIkit: any;

@Component({
  selector: 'app-desktop-layout',
  templateUrl: './desktop-layout.component.html',
  styleUrls: ['./desktop-layout.component.scss']
})
export class DesktopLayoutComponent extends BaseComponent implements OnInit {

  product: Product;

  @Input() title: string = environment.appName;

  constructor(protected injector: Injector, private authService: AuthenticationService,
    protected notifierService: NotifierService) {
    super(injector);
  }

  ngOnInit(): void {

    this.setUpNotifier();

    this.utilities.dialogRequest.subscribe((request: ModalRequest) => {
      if (request.modalType == ModalType.ProductOverview) {
        console.log(request.data);
        this.product = <Product>request.data;
        if (request.open)
          this.showModal("#product-modal", () => { });
        else
          this.hideModal("#product-modal", true);
      }
      else if (request.modalType == ModalType.ProductVariant) {
        this.product = <Product>request.data;
        if (request.open)
          this.showModal("#product-variant", () => { });
        else
          this.hideModal("#product-variant", true);
      }
      else if (request.modalType == ModalType.TimingSelection) {
        if (request.open)
          this.showModal("#time-selection", () => { });
        else
          this.hideModal("#time-selection", true);
      }
    })
  }

  currentNotification: any[] = [];

  setUpNotifier() {
    this.apiRequestService.onMessage$.subscribe((res: ResponseObject<any>) => {
      if (res.Data != null && res.Data.status != 0 && res.Message != null && res.Message.trim() != "") {
        this.checkCounterForSimilarMessage(res, () => {
          console.log("notification popup message")
          this.notifierService.notify(
            res.Result == Result.Success ? 'success' :
              ((res.Result == Result.Failure || res.Result == Result.ValidationError || res.Result == Result.Unauthorized) ? 'warning'
                : 'error')
            , res.Message);
          if (res.Data != null && res.Data.callback != null) {
            res.Data.callback();
          }
        });
      }
    })
  }

  checkCounterForSimilarMessage(res, callBack?: any) {
    let existingIndex = this.currentNotification.findIndex(i => i.Message = res.Message && i.Result == res.Result);
    if (existingIndex > -1) {
      setTimeout(() => {
        if (existingIndex < this.currentNotification.length)
          this.currentNotification.splice(existingIndex, 1);
      }, 2000);
    } else {
      this.currentNotification.push(Object.assign({}, res));
      callBack();
    }
    return false;
  }

}
